























































    export default {
        data() {
            return {
				id:'',//用户id
                title: 'list-triplex-row',
                banner: {},
                htmlString: "",
				type:1,//1代表页面类型是新闻
				defaultImgUrl:"../../static/img/headImage.png",
				// defaultImgUrl:"https://construct-party-1256364044.cos.ap-guangzhou.myqcloud.com/video/1.jpg"
				commentList:[],
				pageNum:5,//评论数
				pageStart:0,//评论页数
				text:'',//评论内容
				haveMoreComment:true
            }
        },
        onLoad(event) {
			uni.showLoading({
				title: '加载中'
			});
            // 目前在某些平台参数会被主动 decode，暂时这样处理。
            try {
                this.banner = JSON.parse(decodeURIComponent(event.detailDate));
            } catch (error) {
                this.banner = JSON.parse(event.detailDate);
            }
			console.log(this.banner)
            this.getDetail();
            uni.setNavigationBarTitle({
                title: this.banner.title
            });
			this.getCommentList();
        },
        methods: {
            getDetail() {
                uni.request({
                    url: this.websiteUrl+'uniApp/news/getNews?id=' + this.banner.id,
                    success: (data) => {
                        
                        this.htmlString = data.data.data.newsContent.content.replace(/\\/g, "").replace(/<img/g,
                        "<img style=\"width:100%;\"");
							// this.htmlString = data.data.data.newsContent.content;
                         uni.hideLoading();
                    },
                    fail: () => {
                        console.log('fail');
                    }
                })
            },
			getCommentList(){
				let that = this;
				let data = {
					type:that.type,
					targetId:that.banner.id,
					pageNum:that.pageNum,
					pageStart:that.pageStart
				};
				uni.request({
					url: this.websiteUrl+'uniApp/comment/getCommentListByIdAndType',
					data: data,
					success: (result) => {
						that.commentList = result.data.data.list;
					},
					fail: (data, code) => {
						uni.stopPullDownRefresh();
						console.log('fail' + JSON.stringify(data));
					}
				})
			},
			checklogin(){
				this.id = uni.getStorageSync('id');
				if(this.id == null || this.id == ''){
					return false;
				}else{
					return true;
				}
			},
			send(e) {
				if (!this.checklogin()) {
					uni.showModal({
						content: '请登录后再操作',
						confirmText:'马上登录',
						success: function (res) {
							if (res.confirm) {
								uni.navigateTo({
								    url: '../login/login',
								});
							} else if (res.cancel) {
								
							}
						}
					});
					return false;
				}
				if (!this.text) {
					uni.showToast({
						title: '请输入评论内容',
						icon: 'none'
					});
					return false;
				}
				var self = this;

				var data = {
					userId:self.id,
					type:self.type,
					targetId:self.banner.id,
					parentId:0,
					rootId:0,
					parentUserId:0,
					commentContent:self.text
				};
				
				uni.request({
					url:this.websiteUrl+'uniApp/comment/addComment',
					data: data,
					success: (result) => {
						//评论成功，才重新请求comment列表
						if(result.data.status == 1){
							self.getCommentList();
						}else{
							uni.showToast({
								 title:  result.data.message,
								duration: 2000
							});
						}
					}
				})
			},
			goToCommentDetail(value){
// 				let data = {
// 					id:value.id
// 				};
				uni.navigateTo({
					url: "../study/comment/commentDetail?id=" + value.id
				})
			}
        }
    }
